@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap');

@use '_normalize.scss';

$흰색:#fff;
$진회색:#777;
$검정색:#000;
$연회색:#9b9b9b;
$파랑색:#464a80;
$연두색:#e5ff9e;
$너비:980px;

$작은폰트:12px;
$중간폰트:14px;
$큰폰트:16px;

.height {
    height: 51px;
}

@mixin 라인하이트() {
    line-height: 51px;
}

@mixin 지역별테마별() {
    display: flex;
    gap: 10px;
    box-sizing: border-box;
    padding: 80px 20px 30px;
}

@mixin h3($너비) {
    border: 1px solid #fff;
    border-radius: 20px;
    padding: 10px;
    font-size: $큰폰트;
    text-align: center;
    margin: 0 auto 20px;
    width: $너비;
}

@mixin 어쩌구() {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $흰색;
    font-weight: bold;
}

