.event {
    width: 1120px;
    height: 300px;
    margin: 0 auto;
    padding-top: 40px;
    margin-bottom: 50px;

    >div {
        position: relative;
    }

    .event_h3 {
        color: $검정색;
        text-align: center;
        font-style: italic;
        font-size: ($큰폰트+30px);
        margin-bottom: 20px;
    }

    .event_a {
        display: block;
        position: absolute;
        right: 0;
        bottom: 0;
    }

}

.event_item {
    clear: both;
    color: #333;
    border: 1px solid #ccc;
    width: 364px;
    height: 244px;
    box-sizing: border-box;
    display: block;

    h3 {
        padding: 10px;
        font-size: $중간폰트;
    }

    .event_imgbox {
        position: relative;

        img {
            width: 100%;
        }

        .event_text {
            padding: 10px;
            background: rgba(0, 0, 0, 0.4);
            color: #fff;
            position: absolute;
            bottom: 0;
            width: 100%;
            box-sizing: border-box;
            font-size: $작은폰트;
        }
    }

    .event_period {
        padding: 10px;
        font-size: $작은폰트;
    }
}


.slick-prev,
.slick-next {
    font-size: 0;
    line-height: 0;
    position: absolute;
    top: 50%;
    display: block;
    width: 33px !important;
    height: 33px !important;
    padding: 0;
    transform: translate(0, -50%);
    cursor: pointer;
    color: #333;
    border: none;
    border-radius: 50%;
    outline: none;
    background: #ccc !important;
    background-size: 33px 33px !important;

    &:hover {
        color: #333;
        background: #ccc;
    }
}

.slick-next {
    right: -40px !important;
}

.slick-prev {
    left: -40px !important;
}

.slick-slide {
    padding-left: 8px;
    padding-right: 8px;
    box-sizing: border-box;
}

